<template lang="pug">
#home.bg_main(v-resize="onResize")
  top
  div(ref="target")
  enterprise-partner.i_pt_20
  services
  project
  .line.container
  member.i_mt_20
  articles
  custom-footer
</template>

<script>
import Top from "@/components/home/Top";
import EnterprisePartner from "@/components/home/EnterprisePartner";
import Services from "@/components/home/Services";
import Project from "@/components/home/Project";
import Member from "@/components/home/Member";
import Articles from "@/components/home/Articles";
import Footer from "@/components/common/Footer";

export default {
  name: "Home",
  components: {
    top: Top,
    "enterprise-partner": EnterprisePartner,
    services: Services,
    project: Project,
    member: Member,
    articles: Articles,
    "custom-footer": Footer,
  },
  data() {
    return {
      isTop: false,
      checkScreenWidth: 0,
    };
  },
  mounted() {
    this.checkScreenWidth = this.$el.offsetWidth;

    var target = this.$refs.target;

    if (this.checkScreenWidth > 767) {
      this.$store.commit(
        "setIsNavVisible",
        target.getBoundingClientRect().top <= 0
      );
    } else {
      this.$store.commit("setIsNavVisible", true);
    }
    console.log(target.getBoundingClientRect().top);

    this.$nextTick(() => {
      this.topAction();
    });
  },
  methods: {
    topAction() {
      var target = this.$refs.target;
      window.addEventListener("scroll", () => {
        if (this.checkScreenWidth > 767) {
          this.$store.commit(
            "setIsNavVisible",
            target.getBoundingClientRect().top <= 0
          );
        } else {
          this.$store.commit("setIsNavVisible", true);
        }
      });
    },
    onResize({ width }) {
      var target = this.$refs.target;
      this.checkScreenWidth = width;
      if (width > 767 && target.getBoundingClientRect().top > 0) {
        this.$store.commit("setIsNavVisible", false);
      } else {
        this.$store.commit("setIsNavVisible", true);
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.line {
  border-bottom: 2px solid $color_purple;
  margin-top: 2px;
  position: relative;
}
</style>
